@import url('https://fonts.googleapis.com/css2?family=Federo&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Potta+One&display=swap');

* {
    // font-family: "Open Sans", sans-serif;    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
}

ul,
li,
dl {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
    display: inline-block;

    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

p {
    margin: 0;
    padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.app {
    width: 1100px;
    margin: 0 auto;
    padding: 50px 0 50px 0;
    position: relative;

    .bg-decoration {
        position: absolute;
        right: -174px;
        bottom: -70px;
        z-index: -1;
    }
}

.container {
    display: block;
    width: 80%;
    margin: 0 auto;
    height: 100%;
}

.btn {
    width: 122px;
    height: 38px;
    border: none;
    background: #434343;
    font-family: 'Open Sans';
    font-size: 16px;
    letter-spacing: -0.035em;
    color: #FFFFFF;
    cursor: pointer;
    text-transform: uppercase;

}

.favorite {
    fill: #ff0000 !important;
}